.header {
    border-bottom: 1px solid #d2d6df;
    display: flex;
}
.header__container-logo {
  width: 60px;
  height: 50px;
  border-right: 1px solid #d2d6df;
  margin: 8px 8px 8px 12px;
  padding-right: 4px;
}
.logo {
  width: 50px;
  height: 50px;
}
.icon__nav{
    color: #5271ff;
    font-size: 30px;
    margin: auto;
    margin-right: 20px;
    cursor: pointer;
}

@media all and (min-width:1024px) {
    .header__container-logo{
        width: 110px;
        height: 100px;
    }
    .logo {
        width: 100px;
        height: 100px;
    }
    .icon__nav{
        font-size: 35px;
    }
}
